import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.scss']
})
export class ChatComponent implements OnInit {

  constructor() { }


  ngOnInit() {
    $(document).ready(function() {
      $(document).mouseup(function(e) {
        let container = $('.box-end-chat');
        if (!container.is(e.target)
            && container.has(e.target).length === 0) {
            $('#boxEndChat').hide();
        }
      });
      $('#endChat').click(function() {
        $('#boxEndChat').css('display', 'flex');
    });
      let chatHeight = $('.height-box').height();
      $('#stylescrollbar').animate({ scrollTop : chatHeight});

      $('#backChat , #closeBoxEndChat').click(function() {
      $('#boxEndChat').css('display', 'none');

  });
});
}


}
