import { Injectable } from '@angular/core';
import {HttpClient, HttpHandler, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs';
import { SignUpDataModel } from '../models/SignUpDataModel';

@Injectable({
  providedIn: 'root'
})
export class DoctorProfileService {
  constructor(private http: HttpClient) { }
  baseUrl = 'https://newwebservice.elbalto.com';
  signUp(): Observable <SignUpDataModel[]> {
    return this.http.get<SignUpDataModel[]>(`${this.baseUrl}/app/SignUpData`);
  }

}
