import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements OnInit {

  constructor() { }


  ngOnInit() {
    $(document).ready(function() {
  let topNav = $('.topNav').height();
  $('#mainPage').css({marginTop :  topNav });
  $('main').css({marginTop :  topNav + 10 });
/** */
  $('#AddAvailableAppointment').click(function() {
$('#box-Add').css({ display: 'flex' });
});
  $('#AddButton ,#buttonCloseAdd , #modify-back').click(function() {
$('#box-Add').fadeOut(1000);
});
$(".nav-links ul li a").click(function() {
  $("body").css({overflow:"auto"});
})
  $(document).mouseup(function(e) {
  let container = $('.box-chose-time');
  if (!container.is(e.target)
      && container.has(e.target).length === 0) {
      $('#box-Add').hide();
  }
});
});
}

}
